<template>
    <div>
        <!-- 我是关注 -->
        <List class="list"
        v-for="(item,index) of arr"
        :key="item.id"
        :index="index"
        :data="item"
        >

        </List>
    </div>
</template>
<script>
import List from '../components/list'
import axios from '../plugins/axios'
export default {
    data(){
        return{
            arr:[
            {_id:1,title:'11111111111',nikename:'关注1'},
            {_id:2,title:'22222222222',nikename:'关注2'},
            {_id:3,title:'33333333333',nikename:'关注3'}
        ]
        }
        
    },
    components:{
        List
    },
    mounted(){
        axios({
            url:'/api/goods/home',
            
        }).then(
            res => this.arr = res.data.data
        )
    }   
}
</script>
<style scoped>
    .list{
        margin-left: 1rem
    }
</style>