<template>
    <div id="Home">
        <Views
        :data="list"
        >

        </Views>
         <List class="list"
        v-for="(item,index) of arr"
        :key="item.id"
        :index="index"
        :data="item"
      />
      <Loging></Loging>
        
    </div>
</template>
<script>
import Views from '../components/viewpager'
import List from '../components/list'
import Loging from '../components/loging'
// import axios from 'axios';
import axios from '../plugins/axios'
export default {
    data(){
        return{
            arr:[
                {id:1,title:'永远的兄弟',intro:'刀郎'},
                // {id:2,title:'探清水河',name:'杨二郎'},
                // {id:3,title:'火红的沙日朗',name:'要不要买菜'}
            ],
            list:[]
        }
        
    },
    components:{
        Views,
        List,
        Loging
    },
     mounted(){
      axios({
        url:'/api/goods/home',
        params:{_page:1,_limit:3},
      }).then(
        res=>this.arr = res.data.data
      ),
      axios({
          url:'/admin/banner',
          method:'post'
      }).then(
          res=>{
              this.list = res.data.data
              console.log(res.data.data,'轮播图数据')
          }
      )
    }
}
</script>
<style>
.list{
    margin-left: 1rem;
    
}
</style>